import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from 'src/app/pages/home/home.component'
import { AddBlogComponent } from 'src/app/pages/add-blog/add-blog.component'
import { AddAccountComponent } from 'src/app/pages/add-account/add-account.component'
import { LoginComponent } from 'src/app/pages/login/login.component'
import { FullViewComponent } from 'src/app/pages/full-view/full-view.component'

const routes: Routes = [
  {
    path: "",
    component: HomeComponent
  },
  {
    path: "add-blog",
    component: AddBlogComponent
  },
  {
    path: "signup",
    component: AddAccountComponent
  },
  {
    path: "login",
    component: LoginComponent
  },
  {
    path: "home",
    component: HomeComponent
  },
  {
    path: 'full-view/:index',
    component: FullViewComponent
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }